.resume{
    margin-left: 100px;
}
button{
    text-decoration: none;
    border: 2px solid black;
    padding: 0 5px;
    border-radius: 3px;
    &:hover{
        color: blue;
        background-color: aqua;
    }
}
a{
    text-decoration: none;
}
@media (max-width : 768px){
    .resume{
        flex-direction: column;
        align-items: center;
        text-align: center;
    }
}