h3{
    text-align: center;
    font-weight: bold;
    font-size: 30px;
}
.container-data{
    display: flex;
    margin-left: 0px;
}
.img{
    border-radius: 10px;
}
.project-card{
    border:2px solid black;
    margin-inline: 90px;
    border-radius: 10px;
    padding: 0px;
    border-style: none;

    button{
        height: 30px;
        width: 100px;
        margin-bottom: 10px;
        border-radius: 15px;

        &:hover{
            background-color: rgb(9, 9, 9);
            color: black;
        }
    }
    h4{
        font-size: 20px;
    }
    a{
        text-decoration: none;
        color:rgb(6, 192, 248);
    }
}