.Home{
    align-items: center;
    margin-left: 50px;
    margin-top: 80px;
    font-size: 20px;
    padding: 0.2rem;

    h1{
        font-weight: bold;
        font-size: 3rem;
        span{
            color:rgba(93, 3, 94, 0.834);
        }
    }
    p{
        font-size: 1.7rem;

        span{
            color: rgba(93, 3, 94, 0.834);
            font-weight: bold;
        }
    }

    .img{
        display: flex;
    }
    .man{
        margin-left: 200px;
        margin-bottom: 20px;
    }
}
@media(max-width:480px) {
    .Home{
        h1{
            font-size: 2rem;
        }
        p{
            font-size: 1 rem;
        }
    }
}

    @media (max-width: 768px) {
        .hero {
            h1 {
                font-size: 2.5rem;
            }
    
            p {
                font-size: 1.2rem;
            }
        }
    }
