.data{
    display: flex;
    
}
.section{
    object-fit: cover;
    margin-left: 300px;
    margin-top: 130px;
    .input{
        height: 25px;
        width: 250px;
        border-radius: 8px;
        padding: 5 10px;
    }
    textarea{
        width: 250px;
        height: 100px;
        border-radius: 8px;
    }
    button{
        border-radius: 5px;
        background-color:blue;
        height: 25px;
        color: antiquewhite;
    }
}
.details{
    margin-top: 110px;
    margin-left: 130px;
    h5{
        font-size: 20px;
    }
    .p{
        color: rgb(56, 56, 56);
        margin-left: 18px;
    }
}
@media (max-width: 768px) {
    .details {
        flex-direction: column;
        align-items: center;
        text-align: center;
    }
}
@media (max-width: 768px){
    .section {
        flex-direction: column;
        align-items: center;
        text-align: center;
    }
}