.navbar{
    display: flex;
    justify-content: space-between;
    padding: 5px 10px;
    position: sticky;
    background: rgb(242, 175, 75);
    top: 0;
    z-index: 1000;

    .toggleTheme{
        height: 30px;
        width: 120px;
        margin-right: 0px;
    }
    ul{
        list-style: none;
    }
    li{
        margin: 0 20px;
    }

    .nav-links{
        display: flex;
        margin-left: 20px;
    }
    a{
        text-decoration: none;
        border: 1px solid black;
        padding: 0 5px;
        border-radius: 5px;
        color: black;
        &:hover{
            background-color: rgba(0, 255, 255, 0.783);
            color:rgb(79, 4, 148);
        }
    } 

    .toggleTheme{
        background: none;
        margin-top: 15px;
        border: 1px solid black;
        border-radius: 5px;
    }
    i{
        color: rgba(4, 0, 255, 0.735);
        margin-left: 5px;
    }
    h1{
        color: rgb(4, 0, 255);
    }
}
body{
    margin: 0;
}


@media (max-width : 768px){
    .navbar{
        flex-direction: column;
        align-items: center;
        text-align: center;
    }
}